<template>
    <div class="portfolioSpec" >
        <div class="portSpecCols columns is-variable is-4 px-6 mx-6 has-text-dark" :class="{'reverse-columns': (projectInd % 2 > 0), 'mb-6 pb-6': projectInd < projectLast, 'line-below': projectInd < projectLast }">
            <div class="column is-half-desktop content" v-if="projectInd % 2 > 0">
                <h1>
                    {{ projectData.title }}
                </h1>
                <p class="project-data-content" v-html="projectData.desc">
                    <!-- {{ projectData.desc }} -->
                </p>
            </div>
            <div class="column is-half-desktop">
                <b-carousel v-if="projectData.imgs.length > 1" :has-drag="false" :arrow-hover="false" :autoplay="false" :indicator-background="true" :indicator-inside="false" :overlay="gallery" class="carou">
                    <b-carousel-item v-for="(item, i) in projectData.imgs" :key="i">
                        <a class="image" @click="switchGallery(true)">
                            <b-image
                                ratio="1349by654"
                                :src="getImgUrl(item)"
                                :webp-fallback="getImgUrl(item, 'png')"
                            >
                            <b-skeleton
                                class="skeleton-placeholder"
                                slot="placeholder"
                                height="100%"
                            ></b-skeleton>
                            </b-image>
                        </a>
                    </b-carousel-item>
                    <span v-if="gallery" @click="switchGallery(false)" class="modal-close is-large has-background-dark"/>
                    <template slot="indicators" slot-scope="props">
                        <figure class="al image" :draggable="false">
                            <b-image 
                            :draggable="false"
                            :src="getImgUrl(props.i)"
                            :webp-fallback="getImgUrl(props.i, 'png')"
                            :title="props.i">
                            <b-skeleton
                                class="skeleton-placeholder"
                                slot="placeholder"
                                height="100%"
                            ></b-skeleton>
                            </b-image>
                        </figure>
                    </template>
                </b-carousel>
                <b-image
                    v-else
                    :src="getImgUrl(projectData.imgs[0])"
                    :webp-fallback="getImgUrl(projectData.imgs[0], 'png')" 
                    ratio="1349by654"
                    @click.native="toggleImgModal(projectData.folder)"
                    class="clickable-image mt-1"
                >
                    <b-skeleton
                        class="skeleton-placeholder"
                        slot="placeholder"
                        height="100%"
                    ></b-skeleton>
                </b-image>
                <div class="modal" v-if="projectData.imgs.length === 1" :id="projectData.folder + 'Img'">
                    <div class="modal-background" @click="toggleImgModal(projectData.folder)"></div>
                    <div class="modal-content">
                        <!-- <figure class="image is-16by9"> -->
                        <b-image
                        :src="getImgUrl(projectData.imgs[0])" 
                        :webp-fallback="getImgUrl(projectData.imgs[0], 'png')"
                        :alt="projectData.imgs[0]"
                        ratio="1349by654"
                        class="image max-h-100">
                        <b-skeleton
                                class="skeleton-placeholder"
                                slot="placeholder"
                                height="100%"
                            ></b-skeleton>
                        </b-image>
                        <!-- </figure> -->
                    </div>
                    <button class="modal-close is-large color-back-n" aria-label="close" @click="toggleImgModal(projectData.folder)"></button>
                </div>
            </div>
            <div class="column is-half-desktop content" v-if="projectInd % 2 === 0">
                <h1>
                    {{ projectData.title }}
                </h1>
                <p class="project-data-content" v-html="projectData.desc">
                    <!-- {{ projectData.desc }} -->
                </p>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'PortfolioSpec',
    props: {
        projectData: Object,
        projectInd: Number,
        projectLast: Number
    },
    data() {
        return {
            gallery: false
        }
    },
    methods: {
        getImgUrl(value, mode) {
            if(typeof value !== 'number'){
                if(!mode){
                    return require(`../assets/portfolio/${this.projectData.folder}/${value.main}`);
                }else{
                    return require(`../assets/portfolio/${this.projectData.folder}/${value.alt}`);
                }
            }else{
                if(!mode){
                    return require(`../assets/portfolio/${this.projectData.folder}/${this.projectData.imgs[value].main}`);
                }else{
                    return require(`../assets/portfolio/${this.projectData.folder}/${this.projectData.imgs[value].alt}`);
                }
            }
        },
        switchGallery(value) {
            this.gallery = value
            if (value) {
                return document.documentElement.classList.add('is-clipped')
            } else {
                return document.documentElement.classList.remove('is-clipped')
            }
        },
        toggleImgModal(fName){
            document.getElementById(`${fName}Img`).classList.toggle('is-active');
        }
    }
}
</script>

<style scoped>
.is-active .al img {
    border: 1px solid #fff;
}
.al img {
    border: 1px solid transparent;
}
.project-data-content{
    white-space: pre-line;
    text-align: justify;
}
.clickable-image{
    border: 1px solid rgba(0,0,0,.2);
    cursor: pointer;
}
.max-h-100{
    margin-top:10px;
    margin-bottom:10px;
    max-height: 100vh;
    max-width: 100vw;
}
/*
.carou{
    border: 1px solid rgba(94, 94, 94, .2);
} */
.line-below{
    border-bottom: 1px solid rgba(94, 94, 94, .2);
}
.color-back-n{
    background-color: black;
}
.color-back-n:hover{
    background-color: black !important;
}
/*
.carou-b{
    border-bottom: 1px solid rgba(94, 94, 94, .2);
} */

@media only screen and (max-width: 769px) { /* <== You can change this break point as per your  needs */
  .reverse-columns {
    flex-direction: column-reverse;
    display: flex;
  }

  .portSpecCols{
      padding-left: .5rem !important;
      padding-right: .5rem !important;
      margin-left: .5rem !important;
      margin-right: .5rem !important;
  }
}
</style>